import MainTitle from '~/components/shack/MainTitle.vue'
import ThumbCard from '~/components/shack/ThumbCard.vue'
import SearchForm from '~/components/shack/SearchForm.vue'
import AddCreditAll from '~/components/directory/AddCreditAll'
import AddFreeMonths from '~/components/directory/AddFreeMonths'
import { Select, Option } from 'element-ui'
import { getQueryParams } from '../../../util/url'

export default {
  name: 'Directory',
  layout: 'ShackDash',
  async asyncData({ store, $membership, route }) {
    const link = `filter[status]=accepted&include=profile,subscriptions,primaryPlan&page=${
      route.query.page
    }`
    return await $membership
      .getAllMemberships(link)
      .then(({ data, meta, links }) => {
        return {
          data,
          meta,
          links
        }
      })
  },
  components: {
    MainTitle,
    SearchForm,
    ThumbCard,
    AddCreditAll,
    AddFreeMonths,
    [Select.name]: Select,
    [Option.name]: Option
  },
  data: () => ({
    loading: false,
    filter: '',
    options: [
      {
        text: 'All',
        value: ''
      },
      {
        text: 'Founding Member',
        value: '0'
      },
      {
        text: 'Fast Track',
        value: '1'
      },
      {
        text: 'Early Invite',
        value: '2'
      },
      {
        text: 'General Member',
        value: '3'
      }
    ]
  }),
  methods: {
    exportData() {
      this.$membership.exportDirectory().then(res => {
        console.log(res)
        let blob = new Blob([res], { type: 'text/csv' })
        // window.location.href = URL.createObjectURL(blob)

        const a = document.createElement('a')
        const url = URL.createObjectURL(blob)

        // Put the link somewhere in the body
        document.body.appendChild(a)
        a.innerHTML = 'download me'
        a.href = url
        // Set our custom filename
        a.download = 'directory-members.csv'
        // Automatically click the link
        a.click()
      })
    },
    toggleModal(type) {
      this.$bvModal.show(type)
    },
    next() {
      const { next } = this.links
      const params = getQueryParams(next)

      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    prev() {
      const { prev } = this.links
      const params = getQueryParams(prev)

      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    changePage(pageNumber) {
      const params = `?page=${pageNumber}`
      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    searchMember(query) {
      this.loading = !this.loading
      const link = `filter[status]=accepted&include=profile,subscriptions,primaryPlan&filter[search]=${query}&filter[prefix_type]=${
        this.filter
      }`

      this.$membership
        .getAllMemberships(link)
        .then(({ data, meta, links }) => {
          this.data = data
          // this.meta = meta
          this.links = links
          this.loading = !this.loading
        })
        .catch(e => {
          this.loading = !this.loading
          const message = e.response ? e.response.data.message : e.message
          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger'
          })
        })
    },
    filterBy() {
      this.loading = !this.loading
      const link = `filter[status]=accepted&include=profile,subscriptions,primaryPlan&filter[prefix_type]=${
        this.filter
      }`
      this.$membership
        .getAllMemberships(link)
        .then(res => {
          this.loading = !this.loading

          this.data = res.data
          this.links = res.links
          this.meta = this.meta
        })
        .catch(e => {
          this.loading = !this.loading
          const message = e.response ? e.response.data.message : e.message
          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger'
          })
        })
    }
  }
}
